.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.quest_lists {
  text-align: justify;
  list-style-position: inside;
}

#create_button{
  position: absolute;
  bottom: 100px;
  left: 45.1%;
  padding: 5px 5px;
  font-size: 20px;
  border: 3px solid #8AC007;
}

#view_button{
  position: absolute;
  bottom: 100px;
  left: 49%;
  padding: 5px 5px;
  font-size: 20px;
  border: 3px solid #8AC007;
}

#remove_button{
  position: absolute;
  bottom: 100px;
  left: 52%;
  padding: 5px 5px;
  font-size: 20px;
  border: 3px solid #8AC007;
}

#add_view_remove{
  position: absolute;
  bottom: 100px;
  left: 50%
}


